<template>
  <div>
    <!-- desktop -->
    <v-row class="hidden-sm-and-down" justify="center" align="center">
      <v-col cols="12" align="center">
        <v-card
          class="pb-5"
          max-height="950px"
          max-width="700px"
          
          style="border-radius: 20px"
        >
          <v-form
            ref="loginform"
            @submit.prevent="validate_login()"
            style="margin-top: 20%; border-radius: 20px"
          >
            <v-row class="mt-12" align="center" justify="center">
              <v-col cols="12 mt-8" align="center" justify="center">
                <!-- desktop -->
                <v-img
                  class="white--text align-end animated fadeInUp slow hidden-sm-and-down mt-10"
                  width="55%"
                  src="../assets/img/logo_one.png"
                ></v-img>
              </v-col>
              <v-col
                class="animated fadeInUp mt-10"
                cols="12"
                align="center"
                justify="center"
              >
                <v-col class="mt-5 mb-5" cols="10" sm="10">
                  <v-text-field
                    color="teal darken-1 teal--text"
                    style="border-radius: 15px"
                    align-center
                    :rules="usernameRules"
                    v-model="username"
                    height="32"
                    prepend-inner-icon="mdi-account-circle"
                    label="username"
                    single-line
                    solo
                  ></v-text-field>
                  <v-text-field
                    color="teal darken-1 teal--text"
                    solo
                    style="border-radius: 15px"
                    :rules="passwordRules"
                    v-model="password"
                    prepend-inner-icon="mdi-lock"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show1 ? 'text' : 'password'"
                    name="input-10-1"
                    label="password"
                    @click:append="show1 = !show1"
                  ></v-text-field>
                  <v-btn
                    v-on:click="validate_login()"
                    flat
                    :loading="loading"
                    type="submit"
                    style="border-radius: 15px"
                    block
                    color="white--text"
                    class="teal darken-1"
                    large
                    >Login</v-btn
                  >
                </v-col>
              </v-col>
            </v-row>
          </v-form>
        </v-card>
      </v-col>
    </v-row>

    <!-- mobile -->
    <v-row class="hidden-md-and-up" justify="center" align="center">
      <v-col class="" cols="12" align="center">
        <v-form
          ref="loginform"
          @submit.prevent="validate_login()"
          style="margin-top: 20%; border-radius: 20px"
        >
          <v-row class="mt-12" align="center" justify="center">
            <v-col cols="12 mt-8" align="center" justify="center">
              <!-- mobile-->
              <v-img
                class="white--text align-end animated fadeInUp slow hidden-md-and-up mt-5"
                width="70%"
                src="../assets/img/logo_one.png"
              ></v-img>
            </v-col>
            <v-col
              class="animated fadeInUp mt-2"
              cols="11"
              align="center"
              justify="center"
            >
              <v-col class="mt-5 mb-5">
                <v-text-field
                  color="blue darken-1 blue--text"
                  style="border-radius: 15px"
                  align-center
                  v-model="username"
                  :rules="usernameRules"
                  height="32"
                  
                  prepend-inner-icon="mdi-account-circle"
                  label="username"
                  single-line
                  solo
                ></v-text-field>
                <v-text-field
                  color="blue darken-1 blue--text"
                  solo
                  
                  style="border-radius: 15px"
                  v-model="password"
                  :rules="passwordRules"
                  prepend-inner-icon="mdi-lock"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show1 ? 'text' : 'password'"
                  name="input-10-1"
                  label="password"
                  @click:append="show1 = !show1"
                ></v-text-field>
                <v-btn
                  v-on:click="validate_login()"
                  
                  :loading="loading"
                  type="submit"
                  style="border-radius: 15px"
                  block
                  color="white--text"
                  class="teal darken-1"
                  large
                  >Login</v-btn
                >
              </v-col>
            </v-col>
          </v-row>
        </v-form>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Swal from "sweetalert2";
export default {
  name: "Login",
  components: {},
  data() {
    return {
      show1: false,
      loading: false,
      username: undefined,
      password: undefined,
      loading: false,
      counters: 0,
      user_login: "",
      usernameRules: [
        v => !!v || 'username is required',
      ],
      passwordRules: [
        v => !!v || 'password is required',
      ],
    };
  },
  watch: {},
  computed() {},
  created() {},
  mounted() {},
  methods: {
    validate_login() {
      this.loading = true;

      if (this.username === undefined || this.password === undefined) {
        Swal.fire({
          icon: "error",
          text: "Username or Password wrong!",
        });
        this.loading = false;
      } else if (this.username === "1" || this.password === "1") {
        this.button_login();
      } 
      else {
        Swal.fire({
          icon: "error",
          text: "Username or Password wrong!",
        });
        this.loading = false;
      }
    },
    button_login() {
      this.loading = false;
      this.save_user_login();
      this.$router.push("/HomePage");
    },
    save_user_login() {
      var user_login = this.username;
      window.localStorage.setItem(
        "user_login_oneaccess",
        JSON.stringify(user_login)
      );
    },
  },
};
</script>

<style scoped>
.yourElement {
  animation-duration: 3s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
}

.img {
  max-width: 100%;
  height: auto;
  background-size: auto;
}
body {
  overflow: hidden;
  /* background-image: url('../assets/img/background.png'); */
  background-color: #cccccc;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  background-position: center;
}
</style>
